@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,500,500i,700,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{margin:0;padding:0;height:100%;box-sizing:border-box;overflow:hidden}body{background-color:#2DA7E1}button{border-radius:8px;border:none;padding:0.5rem;font-weight:500;background-color:#127cad;color:#ecf8fd;cursor:pointer}.container{width:100%;max-width:1000px;margin:0 auto}.container .inner{width:100%;max-width:calc(100% - (6rem * 2));margin:0 auto}.fullHeight{height:100%}.app{height:100%;max-height:100vh;padding:1rem 0;box-sizing:border-box}.appHeader{margin-bottom:10px}.appHeader .inner{max-height:80px;display:flex}.appHeader .inner img{width:80px;height:80px;object-fit:cover;object-position:center;border-radius:8px;margin-right:10px}#root{height:100%}*{color:#001c29;font-family:"Roboto",sans-serif}.chatContainer{display:flex;flex-direction:column;justify-content:space-between;max-height:calc(100vh - 10rem);width:100%;height:100%;background-color:#001c29;border-radius:8px}.chatDisplay{display:flex;flex-direction:column;overflow-y:auto}.chatMessage{background-color:#89d1f3;padding:0.5rem .5rem;margin:0.5rem 1rem;margin-bottom:0;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:0 8px 8px 0;font-weight:500}.chatMessage:first-child{margin-top:1rem}.chatMessage:last-child{margin-bottom:1rem}.chatMessage--user{background-color:#ecf8fd;color:#001c29;align-self:flex-end;border-radius:8px 0 0 8px}.chatForm{display:flex;height:40px}.chatInput{width:100%;border:none;font-size:16px;padding:0 1rem;border-radius:0 0 0 8px;border:2px solid transparent;background-color:#ecf8fd}.chatInput:focus{outline:none;border:2px solid #89d1f3}.chatSend{min-width:100px;background-color:#89d1f3;border:none;font-size:16px;font-weight:bold;border-radius:0 0 8px 0;border:2px solid transparent;color:#001c29}.chatSend:focus{outline:none}.chatButton{margin-right:10px}.chatButton:last-of-type{margin-right:0}

